import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { UtilService } from 'src/providers/utils/utils.service';
import { ClaimStatusService } from '../claim-status/claimStatusService';
import { PopoverController } from '@ionic/angular';
import { PopoverContentPage } from '../popover-content/popover-content.page';

@Component({
  selector: 'app-claim-status-details',
  templateUrl: './claim-status-details.page.html',
  styleUrls: ['./claim-status-details.page.scss'],
})
export class ClaimStatusDetailsPage implements OnInit {

  
  encryptedPolicyNo: any;

  claimType: any;

  noOfClaim: any;

  claimDetailData: any;
  isDataLoading: boolean = true;;
  errorMsg: any;
  canTryAgain: boolean;
  policyNumber: any;
  lifeAssured: any;
  eventDate: any;

  constructor(
    
    public claimStatusService: ClaimStatusService,
    public utils: UtilService,
    private logger: LoggerServiceProvider,
    public translate: TranslateService,
    public popOverCtrl: PopoverController) {
    this.encryptedPolicyNo = history.state.encryptedPolicyNumber//this.navParams.get('encryptedPolicyNumber');
    this.claimType = history.state.claimType //this.navParams.get('claimType');
    this.noOfClaim = history.state.noOfClaim //this.navParams.get('noOfClaim');
  }

  ngOnInit() {
    console.log('ionViewDidLoad ClaimStatusDetailPage');
    this.getClaimsDetailData();
  }
  // ionViewDidLoad() {
  //   console.log('ionViewDidLoad ClaimStatusDetailPage');
  //   this.getClaimsDetailData();
  // }

  getClaimsDetailData() {
    this.getClaimsDetail("");
  }

  getClaimsDetail(event) {
    if (this.utils.isDeviceOnLine()) {
      this.claimStatusService.getClaimsDetail(this.encryptedPolicyNo, this.claimType, this.noOfClaim).then((res: any) => {
        this.isDataLoading = false;
        this.claimDetailData = res['responseJSON']['claimStatusDetails'];
        this.policyNumber = res['responseJSON'].policyNumber;
        this.lifeAssured = res['responseJSON'].lifeAssured;
        this.eventDate = res['responseJSON'].eventDate;

        if (event != "") {
          event.complete();
        }

      }, error => {
        if (event != "") {
          event.complete();
        }
        this.isDataLoading = false;
        this.canTryAgain = true;
        this.logger.error("Claim status Details --search policy by policynumber servcie catch eror : " + JSON.stringify(error));
        this.errorMsg = this.utils.handleServiceException(error);
      });
    } else {
      if (event != "") {
        event.complete();
      }
      this.claimDetailData = [];
      this.isDataLoading = false;
      this.canTryAgain = true;
      this.errorMsg = this.translate.instant('generic_msgs.network_err');
    }

  }

  async openPopup(myEvent,textVal) {
 
      // let popover1 = this.popOverCtrl.create(PopoverContentPage, { msg: textVal }, { cssClass: 'custom-popover' });
      // popover1.present({
      //   ev: myEvent
      // })
      const popover = await this.popOverCtrl.create({
        component: PopoverContentPage,
        componentProps: { msg: textVal },
        cssClass: 'custom-popover'
      });
      await popover.present(myEvent);
    
  }
}
